@use "sass:math";
@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{

    padding: 1rem 0 3rem 0;

    &.inicio{
        background-position: center center;
        background-size: cover;

        @include m.tablet{
            min-height: 70rem;
        }

        .contenido-header{
            min-height: 45rem;
            @include m.tablet{
                min-height: 70rem;
            }
        }
    }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    h1{
        text-align: left;
        color: v.$blanco;
        font-weight: v.$bold;
        line-height: 1.4;
        max-width: 30rem;
        text-transform: capitalize;
        font-size: 2.4rem;

        @include m.tablet{
            font-size: 3.8rem;
            max-width: 50rem;
        }
    }
}