html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Spartan", sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 85%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Spartan", sans-serif;
  margin: 2.5rem 0;
  text-align: center;
}

h1 {
  font-weight: 900;
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 3.8rem;
  }
}

h2 {
  font-weight: 700;
  font-size: 2.6rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3.4rem;
  }
}

h3 {
  font-weight: 400;
  font-size: 2rem;
}
@media (min-width: 768px) {
  h3 {
    font-size: 3rem;
  }
}

h4 {
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  h4 {
    font-size: 2.6rem;
  }
}

p {
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  p {
    font-size: 2rem;
  }
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.seccion:last-of-type {
  margin-bottom: 0;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.header {
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
.header.inicio .contenido-header {
  min-height: 45rem;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1.4;
  max-width: 30rem;
  text-transform: capitalize;
  font-size: 2.4rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    font-size: 3.8rem;
    max-width: 50rem;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 0.5rem;
  border-radius: 50%;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.light {
  filter: invert(65%);
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
.barra img {
  width: 32rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
  .barra img {
    width: 12rem;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.2s linear;
  height: 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
    height: auto;
  }
}
.navegacion a {
  color: #070056;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #33A78B;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2.5rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion .submenu {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  margin-left: 2.5rem;
  cursor: pointer;
}
.navegacion .dropdown {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.2s linear;
  height: 0;
  position: relative;
}
.navegacion .visible {
  position: absolute;
  visibility: visible;
  opacity: 1;
  height: auto;
  background-color: #383838;
}
.navegacion .visible a {
  text-align: center;
  padding: 0.2rem 2rem;
  margin: 0;
}
.navegacion .visible a:hover {
  background-color: #4a4a4a;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 3rem;
  margin: 1rem 0;
  cursor: pointer;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}