@use "sass:math";
@use 'base/variables' as v;
@use 'base/mixins' as m;

.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet{
        align-items: flex-end;
    }

    .dark-mode-boton{
        width: 3rem;
        background-color: rgba(255,255,255, 0.5) ;
        padding: 0.5rem;
        border-radius: 50%;
        margin-top: 1rem;
        margin-bottom: 1rem;

        &:hover{
            cursor: pointer;
        }

        @include m.tablet{
            margin-top: 0;
        }
    }
}

.light{
    filter: invert(65%);
}

.barra{
    display: flex;
    flex-direction: column;
    padding-top: 3rem;
    align-items: center;

    img{
        width: 32rem;
    }

    @include m.tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;

        img{
            width: 12rem;
        }
    }
}

.navegacion{
   visibility: hidden;
   opacity: 0;
   transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.2s linear;
   height: 0;

    @include m.tablet{
        display: flex;
        align-items: center;
        visibility: visible;
        opacity: 1;
        height: auto;
    }

    a{
        color: v.$asignar;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        &:hover{
            color: v.$verde;
        }

        @include m.tablet{
            margin-right: 2.5rem;

            &:last-of-type{
                margin-right: 0;
            }
        }
    }

    .submenu{
        color: v.$blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        margin-left: 2.5rem;
        cursor: pointer;
    }

    .dropdown{
        visibility: hidden;
        opacity: 0;
        transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.2s linear;
        height: 0;
        position: relative;
    }
    .visible{
        position: absolute;
        visibility: visible;
        opacity: 1;
        height: auto;
        background-color: lighten(v.$grisOscuro, 2);
            a{
                text-align: center;
                padding: 0.2rem 2rem;
                margin: 0;

                &:hover{
                    background-color: lighten(v.$grisOscuro, 9);
                }
            }
    }

}

.mobile-menu{
    @include m.tablet{
        display: none;
    }

    img{
        width: 3rem;
        margin: 1rem 0;
        cursor: pointer;
    }
}

.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
}